import {mapState} from "vuex";
import appCashModel from "../../../components/page-component/app-cash-model/app-cash-model";

export default {
    data() {
        return {
            visible: false,
            mark_num: -1,
            money: 0,
            name: '',
            bank_name: '',
            mobile: '',
            moneyInput: '',
            pay_type: {
                auto: false,
                alipay: false,
                wechat: false,
                bank: false,
                balance: false,
            },
            config: [],
            template_message: [],
            cashTypeModel: false,
            cashType: '',
            loading: false,
        }
    },
    components: {appCashModel,},
    computed: {
        ...mapState({
            mall: state => state.mallConfig.mall,
            custom_setting: state => state.mallConfig.share_setting_custom,
            share_setting: state => state.mallConfig.share_setting,
        }),
        cashName() {
            switch (this.cashType) {
                case 'auto':

                    return '微信零钱';







                case 'wx':
                    return '微信线下打款';
                case 'alipay':
                    return '支付宝线下打款';
                case 'bank':
                    return '银联线下打款';
                case 'balance':
                    return '商城余额';
                default:
                    return;
                    break;
            }
        },
    },
    methods: {
        getMoney() {
            let money = this.config.surplusCash ? this.config.surplusCash : this.config.cash_max_day;
            if (+money < +this.money && +money > -1) {
                this.moneyInput = +money;
            } else {
                this.moneyInput = +this.money;
            }
        },

        payTypeChange(value) {
            this.cashType = value;
            switch (value) {
                case 'auto':
                    this.mark_num = 0;
                    break;
                case 'wx':
                    this.mark_num = 1;
                    break;
                case 'alipay':
                    this.mark_num = 2;
                    break;
                case 'bank':
                    this.mark_num = 3;
                    break;
                case 'balance':
                    this.mark_num = 4;
                    break;
                default:
                    this.mark_num = -1;
                    break;
            }
        },

        subscribe() {
            if (this.mark_num === -1) {
                uni.showToast({title: '请选择提现方式', icon: 'none'});
                return;
            }
            if(this.loading) {
                return false
            }
            this.loading = true;
            this.$subscribe(this.template_message).then(res => {
                this.submit();
            }).catch(res => {
                this.submit();
            });
        },
        submit() {
            let that = this;
            let para = {
                price: that.moneyInput,
                type: 'auto',
            };
            uni.showLoading({
                title: '申请中'
            })

            if (that.mark_num === 4) {
                para.type = 'balance';
            } else if (that.mark_num === 0) {
                para.type = 'auto';
            } else {
                para.name = that.name;
                para.mobile = that.mobile;
                switch (that.mark_num) {
                    case 1:
                        para.type = 'wechat';
                        break;
                    case 2:
                        para.type = 'alipay';
                        break;
                    case 3:
                        para.type = 'bank';
                        para.bank_name = that.bank_name;
                        break;
                }
            }
            that.$request({
                url: that.$api.share.cash,
                data: para,
                method: 'post',
            }).then(response=>{
                this.loading = false;
                uni.hideLoading();
                if(response.code === 0) {
                    uni.showToast({
                        title: response.msg,
                        duration: 1000
                    });
                    setTimeout(() => {
                        uni.redirectTo({
                            url: '/pages/share/cash-detail/cash-detail'
                        });
                    }, 1000);
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                this.loading = false;
                uni.hideLoading();
            });
        },

        setting() {
            let that = this;
            that.$request({
                url: that.$api.share.setting,
            }).then(response=>{
                that.$hideLoading();
                if(response.code === 0) {
                    that.config = response.msg.config;
                    that.template_message = response.msg.template_message
                    for (let i = 0; i < that.config.pay_type.length; i++) {
                        switch (that.config.pay_type[i]) {
                            case 'auto':
                                that.pay_type.auto = true;
                                break;
                            case 'alipay':
                                that.pay_type.alipay = true;
                                break;
                            case 'wechat':
                                that.pay_type.wechat = true;
                                break;
                            case 'balance':
                                that.pay_type.balance = true;
                                break;
                            case 'bank':
                                that.pay_type.bank = true;
                                break;
                        }
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                that.$hideLoading();
            });
        },
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        if (options.money > 0) {
            that.money = options.money;
        }
        that.setting();
        uni.setNavigationBarTitle({
            title: that.custom_setting.menus.cash.name,
        });
    }
}
